export default {
  name: "Login",
  data() {
    return {
      formData: {
        username: "",
        password: ""
      },
    };
  },
  methods: {
    async login() {
      let data = {
        username: this.formData.username,
        password: this.formData.password
      };
      const res = await this.$http.post("/v1/backend/login", data);
      console.log(res);
      if (res.data.code === 200) {
        this.$message({
          type: "success",
          message: res.data.msg
        });
        this.$router.push({
          name: "home",
        });
        localStorage.setItem('user_info', JSON.stringify(res.data.data));
        localStorage.setItem('user', JSON.stringify(this.formData.username));
      } else {
        this.$message({
          type: "error",
          message: res.data.msg
        });
      }
    }
  }
};



